import { SpringConfig, animated, useSpring } from "@react-spring/web";
import { ReactNode, forwardRef } from "react";
import styled from "styled-components";
import { configEaseOutStandard } from "../common";
import { SpringActorContextProvider } from "../contexts";
import { IActorStyle } from "../types";

interface ISpringActor {
  init?: IActorStyle;
  children: ReactNode;
  delay?: number;
  to?: IActorStyle | IActorStyle[];
  config?: SpringConfig | ((key: string) => SpringConfig);
}

export const SpringActor = forwardRef<HTMLDivElement, ISpringActor>(
  (
    {
      children,
      init,
      to,
      config = configEaseOutStandard,
      delay = 0,
      ...events
    },
    ref
  ) => {
    const [spring, controller] = useSpring(
      () => ({
        from: init,
        to,
        onStart: ({ value }, ctrl) => {
          if (value.display === "none") ctrl.start({ display: "flex" });
        },
        onRest: ({ value }, ctrl) => {
          if (value.opacity === 0) ctrl.start({ display: "none" });
        },
        delay,
        config,
      }),
      [to, delay]
    );

    return (
      <SpringActorContextProvider value={{ controller }}>
        <Wrapper {...events} style={spring} ref={ref}>
          {children}
        </Wrapper>
      </SpringActorContextProvider>
    );
  }
);
SpringActor.displayName = "SpringActor";

const Wrapper = styled(animated.div)`
  position: absolute;
  top: 0;
  left: 0;
  width: fit-content;
  height: fit-content;
  display: flex;
  ${(p) => p.theme.flexCSS.center}
`;
